import {FETCH_MOVIES, SEARCH_MOVIES,FETCH_MOVIE,LOADING} from '../ConstantType';
import axios from 'axios'
import {APIKey} from '../../APIKey'

export const searchMovies = text => dispatch => {
    dispatch({
        type : SEARCH_MOVIES,
        payload : text
    })
}

export const fetchMovies = text => dispatch =>{

    axios.get(`https://www.omdbapi.com/?apikey=${APIKey}&s=${text}`).then(
        response =>{
            dispatch({
                type:FETCH_MOVIES,
                payload:response.data
            })
        }
    ).catch(
       err => console.log(err)
    )
}

export const fetchMovie= id => dispatch =>{
    axios.get(`https://www.omdbapi.com/?apikey=${APIKey}&i=${id}`).then(response=>dispatch({
        type:FETCH_MOVIE,
        payload:response.data
    })).catch(e=>console.log(e))
}

export const setLoading = () => {
    return {
      type: LOADING
    };
  };
  